<template>
<transition name="leftIn">
    <div class="foodDetail" v-if="show">
      <scroll :data="results">
          <ul>
            <li v-for="(item,index) in results" :key="index">
              <img v-lazy="item.src" alt="">
            </li>
          </ul>
      </scroll>
    </div>
</transition>
</template>

<script>
import Scroll from '@/base/scroll' // 封装的scroll

export default {
  data () {
    return {
      show: {
        type: Boolean,
        default: false
      },
      results: [{'src': require('../assets/imgs/bar1.jpg')}, {'src': require('../assets/imgs/bar1.jpg')}, {'src': require('../assets/imgs/bar1.jpg')}]

    }
  },
  created () {
    setTimeout(() => {
      this.show = true
    }, 20)
  },
  components: {
    Scroll
  }
}
</script>

<style lang="stylus" scoped>
@import '../assets/styl/mixin'

.leftIn-enter-active
  transition all linear .2s
.leftIn-enter,.leftIn-leave-to
  transform translateX(100%)
.foodDetail
  position fixed
  top 45px
  bottom 60px
  left 0
  width 100%
  z-index 99
  background rgb(244,244,244)
  color $color-text-l
  text-align center
  overflow hidden
  img
    width 100%
</style>



